.center {
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
}

.align_h {
  display: flex;
  flex-direction: row;
  align-items: center;
}

.align_v {
  display: flex;
  flex-direction: column;
  align-items: center;
}

.sizeContainer {
  width: $sizeContainerSize;
  margin: 0 auto;
}

.relativeContainer {
  position: relative;
}

.invisible, .invisible::selection {
  background-color: transparent;
  color: transparent;
}